<template>
  <div class="actionCountChart">
    <canvas
      @click="drillDown($event)"
      id="actionCountChart"
    ></canvas>
    <v-row class="my-3">
      <v-col
        cols="12"
        class="text-end"
      >
        <v-btn
          :disabled="executionLevel === 'EXECUTION'"
          @click="resetChart()"
          class="mx-3"
        >Reset</v-btn>
        <v-btn
          :disabled="executionLevel === 'EXECUTION'"
          @click="rollUp()"
          class="mx-3"
        >Back</v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
// import execution_detail from "../../execution_detail.json";
export default {
  name: "ActionCountChart",
  data() {
    return {
      dataSetJSON: {
        goodActionCount: {
          label: "Good",
          borderColor: "#008000",
          backgroundColor: "#008000",
          borderWidth: 1,
          tension: 0.3,
          fill: true,
        },
        betterActionCount: {
          label: "Average",
          borderColor: "#00FFFF",
          backgroundColor: "#00FFFF",
          borderWidth: 1,
          tension: 0.3,
          fill: true,
        },
        badActionCount: {
          label: "Need Improvements",
          borderColor: "#FF0000",
          backgroundColor: "#FF0000",
          borderWidth: 1,
          tension: 0.3,
          fill: false,
        },
      },
      isActivity: false,
      trace: [],
      current: {},
      executionLevel: "",
      heading: "",
      chartDetails: {},
      summary: {},
    };
  },
  mounted() {
    this.summary = execution_detail;
    this.current = execution_detail;
    this.executionLevel = "EXECUTION";
    this.heading = this.current.name;
    this.chartDetails = this.getChartDetails();
  },
  methods: {
    updateChart() {
      let labels = [];
      let datasets = [];
      let chartData = {};

      let goodCountData = [];
      let betterCountData = [];
      let badCountData = [];
      let countColor = [];

      for (let execution in this.current.nestedExecutions) {
        let currentExecution = this.current.nestedExecutions[execution];
        labels.push(currentExecution.name);
        if (this.executionLevel === "ACTIVITY") {
          goodCountData.push(currentExecution.actionCount);
          this.isActivity = true;
          countColor.push(this.setActivityColor(currentExecution.actionCount));
        } else {
          this.isActivity = false;
          goodCountData.push(currentExecution.goodActionCount);
          betterCountData.push(currentExecution.avgActionCount);
          badCountData.push(currentExecution.badActionCount);
        }
      }

      if (this.isActivity) {
        datasets.push({
          ...this.dataSetJSON.goodActionCount,
          data: goodCountData,
          backgroundColor: countColor,
        });
      } else {
        datasets.push({
          ...this.dataSetJSON.goodActionCount,
          data: goodCountData,
        });
      }
      datasets.push({
        ...this.dataSetJSON.betterActionCount,
        data: betterCountData,
      });
      datasets.push({ ...this.dataSetJSON.badActionCount, data: badCountData });

      chartData.labels = labels;
      chartData.datasets = datasets;

      let config = {
        data: {
          labels: chartData.labels,
          datasets: chartData.datasets,
        },
        options: this.getOptions(this.current),
      };

      this.chartDetails.data = config.data;
      this.chartDetails.options = config.options;
      this.chartDetails.update();
    },

    drillDown(event) {
      let points = this.chartDetails.getElementsAtEventForMode(
        event,
        "nearest",
        { intersect: true },
        true
      );

      if (points.length) {
        if (this.current.executionLevel === "SCENARIO") {
          return;
        }

        if (this.executionLevel !== "EXECUTION") {
          let currentIndex = points[0].index;
          this.current = this.current.nestedExecutions[currentIndex];
          this.executionLevel = this.current.nestedExecutions[0].executionLevel;
          this.trace.push(currentIndex);
        } else {
          this.executionLevel = "SCRIPT";
        }

        this.heading = this.getHeading(this.trace);

        this.updateChart();
      }
    },
    setActivityColor(count) {
      if (count > 10) {
        return "#FF0000";
      } else if (count > 5 && count <= 10) {
        return "#00FFFF";
      } else {
        return "#008000";
      }
    },
    getChartDetails() {
      const canvasForNewChart = document.getElementById("actionCountChart");
      return new Chart(canvasForNewChart, {
        type: "bar",
        data: this.getChartData(this.current, [""]),
        options: this.getOptions(this.current),
      });
    },
    getChartData(obj, labels) {
      return {
        labels: labels,
        datasets: [
          { ...this.dataSetJSON.goodActionCount, data: [obj.goodActionCount] },
          { ...this.dataSetJSON.betterActionCount, data: [obj.avgActionCount] },
          { ...this.dataSetJSON.badActionCount, data: [obj.badActionCount] },
        ],
      };
    },
    getHeading(trace) {
      if (trace.length > 0) {
        let headings = [this.summary.name];
        let executionElements = this.summary;

        for (let i = 0; i < trace.length; i++) {
          executionElements = executionElements.nestedExecutions[trace[i]];
          headings.push(executionElements.name);
        }

        return headings.join(" - ");
      }
      this.executionLevel = "SCRIPT";
      return this.current.name;
    },
    getOptions() {
      return {
        scales: {
          x: {
            grid: {
              color: "#4c4c4c",
            },
          },
          y: {
            position: "left",
            ticks: {
              stepSize: 0.5,
              beginAtZero: true,
              font: {
                size: 12,
              },
              color: "white",
            },
            title: {
              display: true,
              text: "No of Activities",
              font: {
                size: 16,
                weight: "bold",
                color: "rgba(50,50,50,0.75)",
              },
            },
            grid: {
              color: "#4c4c4c",
            },
          },
        },

        responsive: true,
        interaction: {
          intersect: false,
        },
        plugins: {
          tooltip: {
            mode: "index",
            position: "nearest",
          },
          title: {
            display: true,
            text: this.heading,
            font: {
              size: 18,
              weight: "bold",
              color: "rgba(50,50,50,0.95)",
            },
          },
          subtitle: {
            display: true,
            text: this.executionLevel,
            position: "bottom",
            align: "center",
            font: {
              size: 20,
              weight: "bold",
              color: "rgba(50,50,50,0.95)",
            },
          },
          legend: {
            display: true,
            position: "top",
            labels: {
              boxWidth: 10,
              padding: 25,
              usePointStyle: true,
            },
          },
        },
      };
    },
    resetChart() {
      this.executionLevel = "EXECUTION";
      this.heading = this.current.name;
      this.current = execution_detail;
      this.chartDetails.data = this.getChartData(this.current, [""]);
      this.chartDetails.options = this.getOptions(this.current);
      this.chartDetails.update();
      this.trace = [];
    },

    rollUp() {
      if (this.trace.length < 0) {
        alert("Invalid operation.");
        return;
      }

      if (this.trace.length < 1) {
        this.executionLevel = "EXECUTION";
        this.resetChart();
        return;
      }

      this.current = execution_detail;
      for (let i = 0; i < this.trace.length - 1; i++) {
        this.current = this.current.nestedExecutions[this.trace[i]];
        this.executionLevel = this.current.nestedExecutions[0].executionLevel;
      }

      this.trace.pop();
      this.heading = this.getHeading(this.trace);
      this.updateChart();
    },
  },
};
</script>

<style>
.actionCountChart {
  /* height: 500px; */
  width: 80%;
  margin: auto;
}
#actionCountChart {
  /* height: 500px !important; */
  width: 100% !important;
}
</style>