      :offset-x="offset"
      :close-on-content-click="closeOnClick"
    >
      <v-card class="elevation-2">
        <v-card-title>{{ msgMenu.selected.commandType }} |
          {{ msgMenu.selected.command }}</v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-chip
            v-for="(param, index) in msgMenu.selected.params"
