<script>
import ActionCountChart from "./ActionCountChart.vue";
import TimeSeriesChart from "./TimeSeriesChart.vue";

export default {
  name: "Complexity",
  components: { ActionCountChart, TimeSeriesChart },
  data() {
    return {
      scriptItems: [],
      headers: [],
      expanded: [],
      singleExpand: false,
      isExpanded: false,
      scripts: [],
      dummyScripts: [],
      activities: [],

      scenarioItems: [],

      isActivity: false,

      hideOverlay: false,
      closeOnClick: false,
      offset: true,
      msgMenu: {
        isShown: false,
        x: 0,
        y: 0,
        selected: {},
      },
    };
  },
  mounted() {
    this.initialiseUI();
  },
  methods: {
    initialiseUI() {
      if (window.complexity) {
        const scripts = window.complexity;

        // this.dummyScripts = scripts.map((scr) => {
        //   return {
        //     id: this.$uuid.v4(),
        //     name: scr.scriptName,
        //     onlyName: scr.scriptName.split(".")[0],
        //     scenariosCount: scr.scenarios.length,
        //     tasks: this.getSumOfTasks(scr),
        //     actions: this.getSumOfActions(scr),
        //     validity: this.getScriptValidity(scr),
        //     userActionTime: this.getUserActionTimeForScript(scr),
        //     systemDelay: this.getSystemDelayForScript(scr),
        //     outcome: this.getScriptOutcome(scr),
        //     networkCalls: this.getNetworkCallsForScript(scr),
        //     scenarios: scr.scenarios.map((scn) => {
        //       return {
        //         id: this.$uuid.v4(),
        //         name: scn.scenarioName,
        //         activitiesCount: scn.activities.length,
        //         actions: this.getActionsInScenario(scn),
        //         validity: this.getScenarioValidity(scn),
        //         userActionTime: this.getUserActionTimeForScenario(scn),
        //         systemDelay: this.getSystemDelayForScenario(scn),
        //         outcome: this.getScenarioOutcome(scn),
        //         networkCalls: this.getNetworkCallsForScenario(scn),
        //         activities: scn.activities.map((act) => {
        //           return {
        //             id: this.$uuid.v4(),
        //             name: act.activityName,
        //             messagesCount: act.actionCount,
        //             validity: this.checkValidity(act),
        //             userActionTime: this.getUserActionTimeForTask(act),
        //             systemDelay: this.getSystemDelayForTask(act),
        //             outcome: act.outCome ? "Complete" : "Incomplete",
        //             networkCalls: this.getNetworkCallsForTask(act),
        //             messages: act.actionMessagesList.map((msg) => {
        //               return {
        //                 id: this.$uuid.v4(),
        //                 message: msg.insightMessage
        //                   ? msg.insightMessage
        //                   : "(no message)",
        //                 validity: msg.isPassed ? "Valid" : "Invalid",
        //                 userActionTime: this.millisecondsToStr(
        //                   msg.userActionTime
        //                 ),
        //                 systemDelay: this.millisecondsToStr(msg.systemDelay),
        //                 networkCalls: msg.networkEntries
        //                   ? msg.networkEntries
        //                   : "-",
        //               };
        //             }),
        //           };
        //         }),
        //       };
        //     }),
        //   };
        // });

        this.scripts = scripts.map((scr) => {
          return {
            id: this.$uuid.v4(),
            name: scr.scriptName,
            scenariosCount: scr.scenarios.length,
            scenarios: scr.scenarios.map((scn) => {
              return {
                id: this.$uuid.v4(),
                name: scn.scenarioName,
                activitiesCount: scn.activities.length,
                activities: scn.activities.map((act) => {
                  return {
                    id: this.$uuid.v4(),
                    name: act.activityName,
                    messagesCount: act.actionCount,
                    ...act,
                    actionMessagesList: act.actionMessagesList.map((msg) => {
                      return {
                        id: this.$uuid.v4(),
                        ...msg,
                      };
                    }),
                  };
                }),
              };
            }),
          };
        });
      } else {
        this.$router.push({ name: "upload" });
      }

      this.headers = [
        {
          text: "Scenarios",
          value: "scenario",
        },
        { text: "Tasks", value: "task" },
        { text: "Steps", value: "step" },
        { text: "Actions", value: "action" },
        { text: "Validity", value: "validity" },
        { text: "Outcome", value: "outcome" },
        { text: "User Action Time", value: "userActionTime" },
        { text: "System Delay", value: "systemDelay" },
        { text: "Network Calls", value: "networkCalls" },
      ];

      this.createTableData();
    },
    createTableData() {
      this.scripts.forEach((scr) => {
        const script = {
          id: scr.id,
          scenario: this.formatScriptName(scr.name),
          task: scr.scenarios.length,
          step: this.getSumOfTasks(scr),
          action: this.getSumOfActions(scr),
          validity: this.getScriptValidity(scr),
          outcome: this.getScriptOutcome(scr),
          userActionTime: this.getUserActionTimeForScript(scr),
          systemDelay: this.getSystemDelayForScript(scr),
          networkCalls: this.getNetworkCallsForScript(scr),
        };

        this.scriptItems.push(script);

        scr.scenarios.forEach((scn) => {
          const scenario = {
            id: scn.id,
            scenario: "",
            task: scn.name,
            step: scn.activities.length,
            action: this.getActionsInScenario(scn),
            validity: this.getScenarioValidity(scn),
            userActionTime: this.getUserActionTimeForScenario(scn),
            systemDelay: this.getSystemDelayForScenario(scn),
            outcome: this.getScenarioOutcome(scn),
            networkCalls: this.getNetworkCallsForScenario(scn),
          };

          this.scriptItems.push(scenario);

          scn.activities.forEach((act) => {
            const activity = {
              id: act.id,
              scenario: "",
              task: "",
              step: act.name,
              action: act.actionCount,
              validity: this.checkValidity(act),
              userActionTime: this.getUserActionTimeForTask(act),
              systemDelay: this.getSystemDelayForTask(act),
              outcome: act.outCome ? "Complete" : "Incomplete",
              networkCalls: this.getNetworkCallsForTask(act),
            };
            this.scriptItems.push(activity);

            act.actionMessagesList.forEach(async (msg) => {
              const item = {
                id: msg.id,
                scenario: "",
                task: "",
                step: "",
                action: msg.insightMessage
                  ? msg.insightMessage
                  : "(no message)",
                validity: msg.isPassed ? "Valid" : "Invalid",
                outcome: "-",
                userActionTime: this.millisecondsToStr(msg.userActionTime),
                systemDelay: this.millisecondsToStr(msg.systemDelay),
                networkCalls:
                  msg.networkEntries.length === 0
                    ? "-"
                    : msg.networkEntries.length,
              };
              this.scriptItems.push(item);
            });
          });
        });
      });
    },
    formatScriptName(name) {
      return name.split(".")[0];
    },
    expandAll() {
      if (this.isExpanded) {
        this.expanded = [];
      } else {
        this.expanded = this.activities;
      }
      this.isExpanded = !this.isExpanded;
    },
    checkValidity(activity) {
      const isNotPassed = (el) => !el.isPassed;

      if (activity.actionMessagesList.some(isNotPassed)) {
        return "Invalid";
      } else {
        return "Valid";
      }
    },

    validityColor(activity) {
      const isNotPassed = (el) => !el.isPassed;

      if (activity.actionMessagesList.some(isNotPassed)) {
        return "red";
      } else {
        return "green";
      }
    },
    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActions(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.actionCount;
        });
      });
      return sum;
    },
    getActionsInScenario(scn) {
      let sum = 0;

      scn.activities.forEach((act) => {
        sum = sum + act.actionCount;
      });

      return sum;
    },
    getScenarioUserActionTime(scenario) {
      let sum = 0;
      scenario.activities.forEach((act) => {
        sum = sum + act.executionTime;
      });
      return this.millisecondsToStr(sum);
    },
    getScriptUserActionTime(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.executionTime;
        });
      });
      return this.millisecondsToStr(sum);
    },
    getScenarioValidity(scenario) {
      let result = [];
      scenario.activities.forEach((act) => {
        result.push(this.checkValidity(act));
      });
      const isNotPassed = (el) => el === "Invalid";

      if (result.some(isNotPassed)) {
        return "Invalid";
      } else {
        return "Valid";
      }
    },
    getScriptValidity(script) {
      let result = [];
      script.scenarios.forEach((scn) => {
        result.push(this.getScenarioValidity(scn));
      });

      const isNotPassed = (el) => el === "Invalid";

      if (result.some(isNotPassed)) {
        return "Invalid";
      } else {
        return "Valid";
      }
    },
    getScenarioOutcome(scenario) {
      const outcome = (el) => !el.outCome;

      if (scenario.activities.some(outcome)) {
        return "Incomplete";
      } else {
        return "Complete";
      }
    },
    getScriptOutcome(script) {
      let result = [];
      script.scenarios.forEach((scn) => {
        result.push(this.getScenarioOutcome(scn));
      });

      const outcome = (el) => el === "Incomplete";

      if (result.some(outcome)) {
        return "Incomplete";
      } else {
        return "Complete";
      }
    },
    getSystemDelayForTask(activity) {
      let sum = 0;
      activity.actionMessagesList.forEach((msg) => {
        sum = sum + msg.systemDelay;
      });
      return this.millisecondsToStr(sum);
    },
    getUserActionTimeForTask(activity) {
      let sum = 0;
      activity.actionMessagesList.forEach((item) => {
        sum = sum + item.userActionTime;
      });
      return this.millisecondsToStr(sum);
    },
    getUserActionTimeForScenario(scenario) {
      let sum = 0;
      scenario.activities.forEach((act) => {
        act.actionMessagesList.forEach((item) => {
          sum = sum + item.userActionTime;
        });
      });
      return this.millisecondsToStr(sum);
    },
    getUserActionTimeForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((item) => {
            sum = sum + item.userActionTime;
          });
        });
      });
      return this.millisecondsToStr(sum);
    },
    getSystemDelayForScenario(scenario) {
      let sum = 0;
      scenario.activities.forEach((act) => {
        act.actionMessagesList.forEach((msg) => {
          sum = sum + msg.systemDelay;
        });
      });
      return this.millisecondsToStr(sum);
    },
    getSystemDelayForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((msg) => {
            sum = sum + msg.systemDelay;
          });
        });
      });
      return this.millisecondsToStr(sum);
    },
    getNetworkCallsForTask(activity) {
      let sum = 0;
      activity.actionMessagesList.forEach((msg) => {
        sum = sum + msg.networkEntries.length;
      });

      return sum;
    },
    getNetworkCallsForScenario(scenario) {
      let sum = 0;
      scenario.activities.forEach((act) => {
        act.actionMessagesList.forEach((msg) => {
          sum = sum + msg.networkEntries.length;
        });
      });
      return sum;
    },
    getNetworkCallsForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((msg) => {
            sum = sum + msg.networkEntries.length;
          });
        });
      });
      return sum;
    },
    showMsgMenu(e, item) {
      e.preventDefault();
      this.msgMenu.isShown = false;
      this.msgMenu.x = e.clientX;
      this.msgMenu.y = e.clientY;
      this.timer = setTimeout(() => {
        this.$nextTick(() => {
          this.msgMenu.isShown = true;
          this.msgMenu.selected = item;
        });
      }, 100);
    },
    millisecondsToStr(milliseconds) {
      var temp = milliseconds / 1000;
      return `${temp}s`;
    },

    itemRowBackground: function (item) {
      if (item.actionCount > 5 && item.actionCount <= 10) {
        return "item-orange";
      } else if (item.actionCount > 10) {
        return "item-red";
      }
    },
    openPdf() {
      window.open("sample.pdf");
    },
  },
};
</script>

