<template>
  <div class="my-6">
    <v-row
      justify="center"
      align="center"
      style="height: 100vh"
    >
      <v-col
        cols="8"
        class="text-center"
      >
        <div
          @click="onJsonUpload"
          class="dropzone-container"
        >
          <div class="file-selector">
            Click here to select your JSON file.
          </div>
        </div>
        <input
          ref="uploader"
          class="d-none"
          type="file"
          @change="onFileChanged"
        >
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "Upload",
  data() {
    return {
      enabled: [],
    };
  },
  methods: {
    onFileChanged(ev) {
      this.selectedFile = ev.target.files[0];
      const reader = new FileReader();
      reader.readAsText(this.selectedFile);
      reader.onload = (e) => {
        window.complexity = this.findItem(e.target.result, "complexity");
        window.i18N = this.findItem(e.target.result, "i18N");
        window.accessibility = this.findItem(e.target.result, "accessibility");
        window.networkLoad = this.findItem(e.target.result, "networkLoad");
        window.browserPerformance = this.findItem(
          e.target.result,
          "browserPerformance"
        );
        window.header = this.findItem(e.target.result, "header");
        this.enabled.push("summaryReport");
        this.$store.commit("setLoaded", true);
        this.$router.push({ name: "home" });
      };
    },
    onJsonUpload() {
      window.addEventListener("focus", () => {}, { once: true });
      this.$refs.uploader.click();
    },
    findItem(array, type) {
      const result = JSON.parse(array).find((o) => o.category === type);
      if (result) {
        this.enabled.push(type);
        this.$store.commit("setEnabled", this.enabled);
        return result.data;
      }
    },
  },
};
</script>

<style>
.file-selector {
  margin: auto;
  color: black;
}

.dropzone-container {
  display: flex;
  flex-direction: column;
  border: 1px dashed #ccc;
  border-radius: 15px;
  height: 100px;
  color:#455a64;
}
</style>