<template>
  <div>
    <div class="text-center my-1">
      <v-row>
        <v-col cols="12">
          <h1 class="darkgreen--text darken-1">Browser Performance Metrics</h1>
        </v-col>
      </v-row>
    </div>
    <v-divider class="my-4"></v-divider>

    <v-expansion-panels
      v-model="panel"
      multiple
      focusable
      readonly
    >
      <v-expansion-panel
        v-for="(item, i) in scenarios"
        :key="i"
      >
        <v-expansion-panel-header hide-actions class="expansionPanelHeader">
          {{ scriptOnlyName }} / {{ item.name }}
        </v-expansion-panel-header>
        <v-expansion-panel-content class="expansionPanel">
          <v-data-table
            :headers="headers"
            :items="item.activities"
            disable-pagination
            class="elevation-1 browserMetricstable"
            hide-default-footer
            disable-sort
          >
          </v-data-table>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
export default {
  name: "BrowserMetrics",
  data() {
    return {
      scripts: [],
      scriptName: "",
      scriptOnlyName:"",
      errors: [],
      hideOverlay: false,
      scriptItems: [],
      headers: [],
      scenarios: [],
      panel: [],
    };
  },

  mounted() {
    if (window.browserPerformance) {
      const scripts = window.browserPerformance;
      this.scripts = scripts;
      this.scriptName = this.scripts[0].scriptName;
      this.scriptOnlyName = this.scripts[0].scriptName.split('.')[0];
      this.scenarios = scripts[0].scenarios;

      this.createTableData();
    } else {
      this.$router.push({ name: "upload" });
    }
  },
  methods: {
    createTableData() {
      this.scenarios.forEach((_, index) => {
        this.panel.push(index);
      });
      let tmpScenarios = [];
      this.scenarios.forEach((scn) => {
        let actItems = [];
        scn.activities.forEach((act) => {
          act.browserPerformanceList.forEach((step, index) => {
            let item = {};
            const {
              row,
              command,
              description,
              parameters,
              TTFB,
              TTLB,
              domInteractive,
              contentDownload,
              firstInteractive,
              firstResponseTime,
              latency,
              networkOverhead,
              pageCompleteTime,
              pageReady,
            } = step;

            if (index === 0) {
              item = {
                activity: act.name,
                row,
                command,
                parameters,
                description,
                ttfb: TTFB,
                ttlb: TTLB,
                domInteractive,
                pageCompleteTime,
                latency,
                networkOverhead,
                firstResponseTime,
                contentDownload,
                firstInteractive,
                pageReady,
              };
              actItems.push(item);
            } else {
              item = {
                activity: "",
                row,
                command,
                parameters,
                description,
                ttfb: TTFB,
                ttlb: TTLB,
                domInteractive,
                pageCompleteTime,
                latency,
                networkOverhead,
                firstResponseTime,
                contentDownload,
                firstInteractive,
                pageReady,
              };
              actItems.push(item);
            }
          });
        });
        tmpScenarios.push({ name: scn.scenarioName, activities: actItems });
      });

      this.scenarios = tmpScenarios;

      setTimeout(() => {
        this.computedHeaders();
      }, 100);
    },
    computedHeaders() {
      this.headers = [
        { text: "Activity", value: "activity" },
        // { text: "Row", value: "row" },
        // { text: "Command", value: "command" },
        { text: "Description", value: "description" },
        // { text: "Parameters", value: "parameters" },
        { text: "TTFB", value: "ttfb" },
        { text: "TTLB", value: "ttlb" },
        { text: "Dom Interactive", value: "domInteractive" },
        { text: "Page Complete Time", value: "pageCompleteTime" },
        { text: "Network Latency", value: "latency" },
        { text: "Network Overhead", value: "networkOverhead" },
        { text: "First Response", value: "firstResponseTime" },
        { text: "Content Download", value: "contentDownload" },
        { text: "First Interactive", value: "firstInteractive" },
        { text: "Page Ready", value: "pageReady" },
      ];
      const step = this.scenarios[0].activities[0];

      // remove keys that are not present in the object
      Object.keys(step).forEach((key) =>
        step[key] === undefined ? delete step[key] : {}
      );

      const keys = Object.keys(step);
      const res = this.headers.filter((item) => keys.includes(item.value));
      this.headers = res;
    },
  },
};
</script>

