<template>
  <div>
    <h1 class="pa-3">Quantitative User Metrics</h1>
    <h3 class="pa-3">AccionLabs</h3>

    <div class="d-flex justify-center align-center ">
      <v-card
        width="50%"
        class="featureListSummaryCard"
      >

        <div
          v-for="(page, index) in pagesList"
          :key="index"
        >
          <v-row class="pa-5">
            <v-col
              cols="6"
              class="d-flex justify-start align-center"
            >
              <h3>{{page.name}}</h3>
            </v-col>
            <v-col
              cols="6"
              class="d-flex justify-end align-center"
            >
              <v-btn
                @click="showDialog(page.type)"
                class="darkgreen darken-1 white--text"
              >Summary of Report</v-btn>
            </v-col>
          </v-row>
          <v-divider></v-divider>

        </div>

      </v-card>
    </div>

    <!-- Feature list dialog -->
    <v-dialog
      v-model="featureDialog"
      width="500"
    >
      <v-card>
        <v-card-title class="text-h5 darkgreen darken-1 mb-4">
          <v-row>
            <v-col cols="11">
              <span class="white--text">Feature List Summary</span>
            </v-col>
            <v-col cols="1">
              <v-btn
                icon
                @click="featureDialog = false"
              ><v-icon small>fa fa-close</v-icon></v-btn>
            </v-col>
          </v-row>
        </v-card-title>

        <v-card-text>
          <div>Number of Scenarios: <b>{{featureListData.scenarios}}</b> </div>
          <div>Total Number of Tasks: <b>{{featureListData.tasks}}</b></div>
          <div>Total Number of Steps: <b>{{featureListData.steps}}</b></div>
          <div>Total Number of Actions: <b>{{featureListData.actions}}</b></div>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="darkgreen darken-1"
            class="white--text"
            @click="openTab('feature')"
          >
            See Details
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- responsiveness list dialog -->
    <v-dialog
      v-model="responsivenessDialog"
      width="500"
    >
      <v-card>

        <v-card-title class="text-h5 darkgreen darken-1 mb-4">

          <v-row>
            <v-col cols="11">
              <span class="white--text">Complexity Summary</span>
            </v-col>
            <v-col cols="1">
              <v-btn
                icon
                @click="responsivenessDialog = false"
              ><v-icon small>fa fa-close</v-icon></v-btn>
            </v-col>
          </v-row>
        </v-card-title>

        <v-card-text>
          <div>Total Number of Tasks: <b>{{complexityReportData.tasks}}</b></div>
          <div>Average User Action Time Per Task: <b>{{complexityReportData.avgUserActionTime}}</b></div>
          <div>Average System Delay Per Task: <b>{{complexityReportData.avgSystemDelay}}</b></div>
          <div>Average Network Calls Per Task: <b>{{complexityReportData.avgNetworkCalls}}</b></div>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="darkgreen darken-1"
            class="white--text"
            @click="openTab('complexity')"
          >
            See Details
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- i18n list dialog -->
    <v-dialog
      v-model="internationalizationDialog"
      width="500"
    >
      <v-card>

        <v-card-title class="text-h5 darkgreen darken-1 mb-4">

          <v-row>
            <v-col cols="11">
              <span class="white--text">Internationalization Summary</span>
            </v-col>
            <v-col cols="1">
              <v-btn
                icon
                @click="internationalizationDialog = false"
              ><v-icon small>fa fa-close</v-icon></v-btn>
            </v-col>
          </v-row>
        </v-card-title>

        <v-card-text>
          <div>Total Number of Tasks: <b>{{i18NReportData.tasks}}</b></div>
          <div>Languages Supported : <b>{{i18NReportData.languagesSupported}}</b></div>
          <div>Average Number of Violations Per Task for English : <b>{{i18NReportData.avgViolations}}</b></div>

        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="darkgreen darken-1"
            class="white--text"
            @click="openTab('i18N')"
          >
            See Details
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- wcag list dialog -->
    <v-dialog
      v-model="accessibilityDialog"
      width="500"
    >
      <v-card>

        <v-card-title class="text-h5 darkgreen darken-1 mb-4">
          <v-row>
            <v-col cols="11">
              <span class="white--text">Accessibility Summary</span>
            </v-col>
            <v-col cols="1">
              <v-btn
                icon
                @click="accessibilityDialog = false"
              ><v-icon small>fa fa-close</v-icon></v-btn>
            </v-col>
          </v-row>
        </v-card-title>

        <v-card-text>
          <div>Total Number of Tasks: {{accessibilityReportData.tasks}}</div>
          <div>Average Number of Violations Per Task for Category Critical : {{ accessibilityReportData.criticalVialations }}</div>
          <div>Average Number of Violations Per Task for Category Serious: {{ accessibilityReportData.seriousViolations }}</div>
          <div>Average Number of Violations Per Task for Category Moderate: {{ accessibilityReportData.moderateViolations }}</div>
          <div>Average Number of Violations Per Task for Category Minor: {{ accessibilityReportData.minorViolations }}</div>

        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="darkgreen darken-1"
            class="white--text"
            @click="openTab('accessibility')"
          >
            See Details
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "SummaryReport",
  data() {
    return {
      featureDialog: false,
      responsivenessDialog: false,
      internationalizationDialog: false,
      accessibilityDialog: false,
      featureListData: {},
      complexityReportData: {},
      i18NReportData: {},
      accessibilityReportData: {},
      pagesList: [
        { name: "Feature List", type: "feature" },
        { name: "Responsiveness Report", type: "complexity" },
        { name: "Internationalization (i18N) Report", type: "i18n" },
        { name: "Accessibility Report", type: "wcag" },
        { name: "Browser Metrics Report", type: "browser" },
      ],
    };
  },
  mounted() {
    const scripts = window.complexity;
    const accessibilityScript = window.accessibility;
    this.featureListData = {
      scenarios: scripts.length,
      tasks: scripts[0].scenarios.length,
      steps: this.getSumOfTasks(scripts[0]),
      actions: this.getSumOfActions(scripts[0]),
    };
    this.complexityReportData = {
      tasks: scripts[0].scenarios.length,
      avgUserActionTime: this.getUserActionTimeForScript(scripts[0],scripts[0].scenarios.length),
      avgSystemDelay: this.getSystemDelayForScript(scripts[0],scripts[0].scenarios.length),
      avgNetworkCalls:
        this.getNetworkCallsForScript(scripts[0]) / scripts[0].scenarios.length,
    };

    const avgViolations =
      this.getScriptWordCount(window.i18N[0]) +
      this.getAltWordCountForScript(window.i18N[0]) +
      this.getAriaLabelWordCountForScript(window.i18N[0]) +
      this.getTooltipWordCountForScript(window.i18N[0]) +
      this.getTitleWordCountForScript(window.i18N[0]);
    this.i18NReportData = {
      tasks: scripts[0].scenarios.length,
      languagesSupported: window.i18N[0].language,
      avgViolations: avgViolations / scripts[0].scenarios.length,
    };

    this.accessibilityReportData = {
      tasks : accessibilityScript[0].scenarios.length,
      seriousViolations : this.getAvgNoOfViolations(accessibilityScript[0].scenarios,'serious'),
      criticalVialations : this.getAvgNoOfViolations(accessibilityScript[0].scenarios,'critical'),
      moderateViolations : this.getAvgNoOfViolations(accessibilityScript[0].scenarios,'moderate'),
      minorViolations : this.getAvgNoOfViolations(accessibilityScript[0].scenarios,'minor'),
    }

    
  },
  methods: {
    openTab(type) {
      let displayName;
      let componentName;

      if (type === "feature") {
        displayName = "Feature List";
        componentName = "FeatureList";
        this.featureDialog = false;
      }
      if (type === "complexity") {
        displayName = "Complexity";
        componentName = "Complexity";
        this.responsivenessDialog = false;
      }
      if (type === "accessibility") {
        displayName = "WCAG";
        componentName = "Accessibility";
        this.accessibilityDialog = false;
      }
      if (type === "i18N") {
        displayName = "Internationalization (I18N)";
        componentName = "Internationalization";
        this.internationalizationDialog = false;
      }

      const tabObject = {
        displayName,
        componentName,
        type,
        icon: "fa-chart-simple",
        hoverText: "",
        children: [],
        disabled: true,
      };
      this.$root.$emit("openTab", tabObject);
    },
    showDialog(type) {
      if (type === "feature") {
        this.featureDialog = true;
      }
      if (type === "complexity") {
        this.responsivenessDialog = true;
      }
      if (type === "i18n") {
        this.internationalizationDialog = true;
      }
      if (type === "wcag") {
        this.accessibilityDialog = true;
      }
      if (type === "browser") {
        const tabObject = {
          displayName: "Browser Performance Metrics",
          componentName: "BrowserMetrics",
          type,
          icon: "fa-chart-simple",
          hoverText: "",
          children: [],
          disabled: true,
        };
        this.$root.$emit("openTab", tabObject);
      }
    },
    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActions(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.actionCount;
        });
      });
      return sum;
    },
    getUserActionTimeForScript(script,lengthOfTasks) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((item) => {
            sum = sum + item.userActionTime;
          });
        });
      });
      return this.millisecondsToStr(sum/lengthOfTasks);
    },
    getSystemDelayForScript(script,lengthOfTasks) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((msg) => {
            sum = sum + msg.systemDelay;
          });
        });
      });
      return this.millisecondsToStr(sum/lengthOfTasks);
    },
    getNetworkCallsForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((msg) => {
            if (msg.networkEntries) {
              sum = sum + msg.networkEntries.length;
            }
          });
        });
      });
      return sum;
    },
    millisecondsToStr(milliseconds) {
      var temp = milliseconds / 1000;
      return `${temp}s`;
    },

    getWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { contents } = item;
        sum = sum + contents.length;
      });

      return sum;
    },
    getAltWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { alts } = item;
        sum = sum + alts.length;
      });

      return sum;
    },
    getTitleWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { tittle } = item;
        sum = sum + tittle.length;
      });

      return sum;
    },
    getAriaLabelWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { ariaLabels } = item;
        sum = sum + ariaLabels.length;
      });

      return sum;
    },
    getToolTipWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { toolTips } = item;
        sum = sum + toolTips.length;
      });

      return sum;
    },
    getActivityWordCount(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getWordCount(item.data);
      });

      return sum;
    },
    getTooltipWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getToolTipWordCount(item.data);
      });

      return sum;
    },
    getAriaLabelWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getAriaLabelWordCount(item.data);
      });

      return sum;
    },
    getAltWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getAltWordCount(item.data);
      });

      return sum;
    },
    getTitleWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getTitleWordCount(item.data);
      });

      return sum;
    },
    getScenarioWordCount(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getActivityWordCount(act);
      });

      return sum;
    },
    getTooltipWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getTooltipWordCountForActivity(act);
      });

      return sum;
    },
    getAriaLabelWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getAriaLabelWordCountForActivity(act);
      });

      return sum;
    },
    getAltWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getAltWordCountForActivity(act);
      });

      return sum;
    },
    getTitleWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getTitleWordCountForActivity(act);
      });

      return sum;
    },
    getScriptWordCount(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getScenarioWordCount(scn);
      });
      return sum;
    },
    getAltWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getAltWordCountForScenario(scn);
      });
      return sum;
    },
    getAriaLabelWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getAriaLabelWordCountForScenario(scn);
      });
      return sum;
    },
    getTooltipWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getTooltipWordCountForScenario(scn);
      });
      return sum;
    },
    getTitleWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getTitleWordCountForScenario(scn);
      });
      return sum;
    },
    getAvgNoOfViolations(scenario,categoryOfVialoation){
      let sumOfViolations = 0;
      scenario.forEach((activities)=>{
        activities.activities.forEach((activity)=>{
          activity.accessibilityData.forEach((action)=>{
            sumOfViolations = sumOfViolations + action.errors.filter((item)=> item.impact == categoryOfVialoation).length;
            
          });
        });
      })
      return (sumOfViolations/scenario.length);
    }
  },
};
</script>

<style>
.featureListSummaryCard {
  background-color: #dae8fc !important;
}
</style>