<template>
  <v-card
    color="grey lighten-4"
    flat
    tile
  >
    <v-toolbar>
      <v-app-bar-nav-icon></v-app-bar-nav-icon>

      <v-toolbar-title>
        <v-row>
          <v-col cols="auto">
            <img
              v-if="image !== ''"
              class="logo ml-3 py-4"
              :src="image"
              alt="logo"
            />
          </v-col>
          <v-col
            v-if="heading"
            cols="auto"
            class="d-flex align-center"
          >
            <div>{{heading}}</div>
          </v-col>

        </v-row>
      </v-toolbar-title>

      <!-- <v-spacer></v-spacer>
      <div class="language-input my-2">

        <v-select
          v-model="$i18n.locale"
          :items="languages"
          label="Select language"
          hide-details
          solo
        ></v-select>
      </div> -->

    </v-toolbar>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    languages: ["en", "fr", "sp"],
    image: "",
    heading: "",
  }),

  mounted() {
    setTimeout(() => {
      // check if local and image exists
      // this scenario might happen when user has only provided the image
      // and hence the header.js file won't be available
      if (location.pathname.includes("index.html")) {
        this.checkIfImageExists("./execution/header-image.jpg", (exists) => {
          if (exists) {
            this.image = "./execution/header-image.jpg";
          }
        });
      }
      if (window.header) {
        this.heading = window.header[0].headerText;
        if (window.header[0].headerImage) {
          this.image = window.header[0].headerImage;
        }
      }
    }, 200);
  },
  methods: {
    checkIfImageExists(url, callback) {
      const img = new Image();
      img.src = url;

      if (img.complete) {
        callback(true);
      } else {
        img.onload = () => {
          callback(true);
        };

        img.onerror = () => {
          callback(false);
        };
      }
    },
  },
};
</script>

<style>
.logo {
  /* width: 150px; */
  height: 80px;
}
.language-input {
  width: 100px;
}

.theme--light.v-toolbar.v-sheet {
    background-color: #DAE8FC !important;
}
</style>