<template>
  <div>

    <div class="my-8">
      <div class="text-center my-1">
        <h1 class="darkgreen--text darken-1">Feature List</h1>
      </div>
      <v-divider class="my-4"></v-divider>
      <v-row class="featureListHeader">
        <v-col
          class="font-weight-bold text-h6"
          cols="3"
        > Scenario </v-col>
        <v-col
          class="font-weight-bold text-h6"
          cols="3"
        >Tasks</v-col>
        <v-col
          class="font-weight-bold text-h6"
          cols="2"
        >Step</v-col>
        <v-col
          class="font-weight-bold text-h6"
          cols="4"
        >Actions</v-col>

      </v-row>

      <div
        v-for="(script, index) in scripts"
        style="width: 100%"
        class="featureListTable mt-2"
      >
        <v-row>
          <v-col
            class="font-weight-bold name"
            cols="3"
          >{{
            script.nameOnly
          }}</v-col>
          <v-col cols="3">{{ script.scenariosCount }}</v-col>
          <v-col cols="2">{{ script.tasks }}</v-col>
          <v-col cols="4">{{ script.actions }}</v-col>
        </v-row>
        <div v-for="(scenario, index) in script.scenarios">
          <v-row>
            <v-col cols="3"></v-col>
            <v-col
              class="font-weight-bold name"
              cols="3"
            >{{
              scenario.name
            }}</v-col>
            <v-col cols="2">{{ scenario.activitiesCount }}</v-col>
            <v-col cols="4">{{ scenario.actions }}</v-col>
          </v-row>
          <div v-for="(activity, index) in scenario.activities">
            <v-row>
              <v-col cols="3"></v-col>
              <v-col cols="3"></v-col>
              <v-col
                class="font-weight-bold name"
                cols="2"
              >{{
                activity.name
              }}</v-col>
              <v-col cols="4">{{ activity.messagesCount }}</v-col>
            </v-row>
            <div v-for="(msg, index) in activity.messages">
              <v-row>
                <v-col cols="3"></v-col>
                <v-col cols="3"></v-col>
                <v-col cols="2"></v-col>
                <v-col cols="4">
                  <div
                    class="pl-2"
                    style="border-left: 1px solid white"
                  >
                    {{ msg.message }}
                  </div>
                </v-col>
              </v-row>
            </div>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
export default {
  name: "FeatureList",

  data() {
    return {
      expanded: [],
      singleExpand: false,
      isExpanded: false,
      scripts: [],
      activities: [],

      hideOverlay: false,

      closeOnClick: false,
    };
  },
  mounted() {
    this.initialiseUI();
  },
  methods: {
    initialiseUI() {
      if (window.complexity) {
        const scripts = window.complexity;

        this.scripts = scripts.map((scr) => {
          return {
            name: scr.scriptName,
            nameOnly: scr.scriptName.split('.')[0],
            scenariosCount: scr.scenarios.length,
            tasks: this.getSumOfTasks(scr),
            actions: this.getSumOfActions(scr),

            scenarios: scr.scenarios.map((scn) => {
              return {
                name: scn.scenarioName,
                activitiesCount: scn.activities.length,
                actions: this.getActionsInScenario(scn),

                activities: scn.activities.map((act) => {
                  return {
                    name: act.activityName,
                    messagesCount: act.actionCount,

                    messages: act.actionMessagesList.map((msg) => {
                      return {
                        message: msg.insightMessage
                          ? msg.insightMessage
                          : "(no message)",
                      };
                    }),
                  };
                }),
              };
            }),
          };
        });
      } else {
        this.$router.push({ name: "upload" });
      }
    },

    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActions(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.actionCount;
        });
      });
      return sum;
    },
    getActionsInScenario(scn) {
      let sum = 0;

      scn.activities.forEach((act) => {
        sum = sum + act.actionCount;
      });

      return sum;
    },

    millisecondsToStr(milliseconds) {
      var temp = milliseconds / 1000;
      return `${temp}s`;
    },
  },
};
</script>

