<template>
  <div class="my-8">
    <div class="text-center my-1">
      <h2 class="heading darkgreen--text darken-1">Network Load</h2>
    </div>
    <v-divider class="my-4"></v-divider>
    <v-row class="headerNetworkLoad">
      <v-col
        class="font-weight-bold text-h6"
        cols="2"
      >
        Scenario
      </v-col>
      <v-col
        class="font-weight-bold  text-h6"
        cols="2"
      >Tasks</v-col>
      <v-col
        class="font-weight-bold  text-h6"
        cols="2"
      >Step</v-col>
      <v-col
        class="font-weight-bold  text-h6"
        cols="2"
      >Actions</v-col>
      <v-col
        class="font-weight-bold  text-h6"
        cols="2"
      >Network Calls</v-col>

    </v-row>
    <div
      v-for="(script, index) in scripts"
      style="width: 100%"
      class="actualTableNetworkLoad"
    >
      <v-row>
        <v-col
          class="font-weight-bold name"
          cols="2"
        >{{script.onlyName}}</v-col>
        <v-col cols="2">{{script.scenariosCount}}</v-col>
        <v-col cols="2">{{script.tasks}}</v-col>
        <v-col cols="2">{{script.actions}}</v-col>
        <v-col cols="2">{{script.entries}}</v-col>

      </v-row>
      <div v-for="(scenario, index) in script.scenarios">
        <v-row>
          <v-col cols="2"></v-col>
          <v-col
            class="font-weight-bold name"
            cols="2"
          >{{scenario.name}}</v-col>
          <v-col cols="2">{{scenario.activitiesCount}}</v-col>
          <v-col cols="2">{{scenario.actions}}</v-col>
          <v-col cols="2">{{scenario.entries}}</v-col>

        </v-row>
        <div v-for="(activity, index) in scenario.activities">
          <v-row>
            <v-col cols="2"></v-col>
            <v-col cols="2"></v-col>
            <v-col
              class="font-weight-bold name"
              cols="2"
            >{{activity.name}}</v-col>
            <v-col cols="2">{{activity.actions}}</v-col>
            <v-col cols="2">{{activity.entries}}</v-col>

          </v-row>
          <div v-for="(log, index) in activity.networkLoad">
            <v-row>
              <v-col cols="2"></v-col>
              <v-col cols="2"></v-col>
              <v-col cols="2"></v-col>
              <v-col cols="2">
                <div
                  class="pl-2"
                  style="border-left: 1px solid white"
                >
                  {{log.description}}
                </div>
              </v-col>
              <v-col cols="1">{{log.entries}}</v-col>
            </v-row>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NetworkLoad",
  data() {
    return {
      scripts: [],
    };
  },
  mounted() {
    this.initializeUI();
  },
  methods: {
    initializeUI() {
      if (window.networkLoad) {
        const scripts = window.networkLoad;

        this.scripts = scripts.map((scr) => {
          return {
            name: scr.scriptName,
            onlyName: scr.scriptName.split('.')[0],
            scenariosCount: scr.scenarios.length,
            tasks: this.getSumOfTasks(scr),
            actions: this.getSumOfActions(scr),
            entries: this.getEntriesOfScripts(scr),
            scenarios: scr.scenarios.map((scn) => {
              return {
                name: scn.scenarioName,
                activitiesCount: scn.activities.length,
                actions: this.getActionsInScenario(scn),
                entries: this.getEntriesOfScenario(scn),
                activities: scn.activities.map((act) => {
                  return {
                    name: act.activityName,
                    actions: act.networkLoads.length,
                    entries: this.getEntriesOfActivities(act),
                    networkLoad: act.networkLoads.map((log) => {
                      return {
                        description: log.description,
                        entries: log.networkCalls.length,
                      };
                    }),
                  };
                }),
              };
            }),
          };
        });
      } else {
        this.$router.push({ name: "upload" });
      }
    },
    getEntriesOfScripts(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getEntriesOfScenario(scn);
      });
      return sum;
    },
    getEntriesOfScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getEntriesOfActivities(act);
      });
      return sum;
    },
    getEntriesOfActivities(act) {
      let sum = 0;
      act.networkLoads.forEach((log) => {
        sum = sum + log.networkCalls.length;
      });
      return sum;
    },
    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActions(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.networkLoads.length;
        });
      });
      return sum;
    },
    getActionsInScenario(scn) {
      let sum = 0;

      scn.activities.forEach((act) => {
        sum = sum + act.networkLoads.length;
      });

      return sum;
    },
  },
};
</script>

