          cols="1"
        >Alt</v-col>
        <v-col
          class="font-weight-bold  text-h6"
          cols="1"
        >Aria Label </v-col>
        <v-col
          class="font-weight-bold  text-h6"
          cols="1"
        >Tool Tip </v-col>
         <v-col
          class="font-weight-bold  text-h6"
          cols="1"
        >Title </v-col>

      </v-row>
      <div
        v-for="(script, index) in scripts"
        style="width: 100%"
        class="actualTable"
      >
        <div v-for="(scenario, index) in script.scenarios">
          <v-row>
            <v-col
              class="font-weight-bold name"
              cols="2"
            >{{scenario.name}}</v-col>
            <v-col cols="2">{{scenario.activitiesCount}}</v-col>
            <v-col cols="3">{{scenario.actions}}</v-col>
            <v-col cols="1">{{scenario.wordCount}}</v-col>
            <v-col cols="1"> {{scenario.altWordCount}}</v-col>
            <v-col cols="1"> {{scenario.ariaLabelWordCount}}</v-col>
            <v-col cols="1"> {{scenario.toolTipWordCount}}</v-col>
            <v-col cols="1"> {{scenario.titleWordCount}}</v-col>
          </v-row>
          <div v-for="(activity, index) in scenario.activities">
            <v-row>
              <v-col cols="2"></v-col>
              <v-col
                class="font-weight-bold name"
                cols="2"
              >{{activity.name}}</v-col>
              <v-col cols="3">{{activity.messagesCount}}</v-col>
              <v-col cols="1"> {{activity.wordCount}}</v-col>
              <v-col cols="1"> {{activity.altWordCount}}</v-col>
              <v-col cols="1"> {{activity.ariaLabelWordCount}}</v-col>
              <v-col cols="1"> {{activity.toolTipWordCount}}</v-col>
              <v-col cols="1"> {{activity.titleWordCount}}</v-col>
            </v-row>
            <div v-for="(data, index) in activity.i18NDataList">
              <v-row>
                <v-col cols="2"></v-col>
                <v-col cols="2"></v-col>
                <v-col cols="3">
                  <div style="display:flex">
                    <div
                      class="pl-2"
                      style="border-left: 1px solid white"
                    >
                      {{data.message}}
                    </div>

                  </div>
                </v-col>
                <v-col cols="1" :class="isScreenshotBtnDisabled(data.elements) ? 'color_red' : 'color_green' "
                      @click="showElements(data.elements)" > {{data.wordCount}}</v-col>
                <v-col cols="1" :class="isScreenshotBtnDisabled(data.alts) ? 'color_red' : 'color_green' "
                      @click="showElements(data.alts)"> {{data.altWordCount}}</v-col>
                <v-col cols="1" :class="isScreenshotBtnDisabled(data.ariaLabels) ? 'color_red' : 'color_green' "
                      @click="showElements(data.ariaLabels)"> {{data.ariaLabelWordCount}}</v-col>
                <v-col cols="1" :class="isScreenshotBtnDisabled(data.toolTips) ? 'color_red' : 'color_green' "
                      @click="showElements(data.toolTips)"> {{data.toolTipWordCount}}</v-col>
                <v-col cols="1" :class="isScreenshotBtnDisabled(data.title) ? 'color_red' : 'color_green' "
                      @click="showElements(data.title)"> {{data.titleWordCount}}</v-col>
              </v-row>
            </div>
          </div>
        </div>
      </div>
      
      
    </div>
    <v-dialog
      v-model="elementDialog"
      content-class="no-dialog-overflow"
      max-width="1000"
      auto-grow
      :hide-overlay="hideOverlay"
    >
      <v-card class="elevation-2 dialog_images">
        <v-card-title class="text-h5">
          <v-row>
            <v-col cols="11">Elements</v-col>
            <v-col
              cols="1"
              class="text-end"
            >
              <v-btn
                @click="elementDialog = false"
                icon
              >
                <v-icon>fas fa-close</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-text>
          <div
            v-for="(element,index) in elements"
            :key="index">

            <v-row>
              <v-col cols="auto">

                <div
                  class="element-screenshot"
                  @click="showImage(element.imagePath)"
                >
                  <v-card
                    :ripple="false"
                    class="portrait i18NImagesCard"
                    v-if="element.imagePath"
                  ><img :src="element.imagePath" class="i18NImages" style="width: auto;height: auto;max-width: 200px;max-height: 150px;"></v-card>
                </div>
              </v-col>
              <v-col cols="8" class="codeSnippetAndWords">
                <v-row>
                  <div class="teal--text">
                    <code>
                      {{ element.htmlSnippet }}
                    </code>
                  </div>
                </v-row>
                <v-divider class="my-4"></v-divider> 
                <v-row>
                  <div class="teal--text">
                    {{ getWordsSepreatedByComma(element.words) }}
                  </div>
                </v-row>  
              </v-col>
               
            </v-row>

          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="imageDialog"
      content-class="no-dialog-overflow"
      fullscreen
      :hide-overlay="hideOverlay"
    >

      <v-card class="elevation-2 dialog_images">
        <v-card-text>
          <div
            @click="imageDialog = false"
            class="text-center"
          >

            <div class="element-screenshot__overlay">
              <div class="element-screenshot">
                <div class="element-screenshot__content">
                  <div
                    class="element-screenshot__image"
                    :style="{'background-size':'100%', 'background-image': `url(${baseImage})`, position: 'relative', width: `${dimension.width}px`, height: `${dimension.height}px`}"
                  >
                  </div>
                </div>
              </div>
            </div>

          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>

</template>

<script>
export default {
  name: "Internationalization",
  data() {
    return {
      elementDialog: false,
      scripts: [],
      showMenu: false,
      closeOnClick: true,
      dimension: {},
      baseImage: "",
      imageDialog: false,
      hideOverlay: false,
      elements: [],
      closeOnClick: false,
      x: 0,
      y: 0,
      img: "",
      totalIssues:"",
      onlyName:"",
      summary_details:[],
      metrics_details:[]
    };
  },
  mounted() {
    this.initialiseUI();
    this.totalIssues = this.scripts[0].wordCount + this.scripts[0].altWordCount + this.scripts[0].toolTipWordCount + this.scripts[0].ariaLabelWordCount + this.scripts[0].titleWordCount;
    this.onlyName = this.scripts[0].name.split('.')[0];
    
    this.summary_details = [
      {
      'key' : 'Name',
      'value' : this.onlyName,
      
      },
      {
      'key' : 'Target language',
      'value' : this.scripts[0].language,
      
      },
      {
        'key' : 'Tasks',
        'value' : this.scripts[0].scenariosCount,
      },
      {
        'key' : 'Steps',
        'value' : this.scripts[0].tasks,
      },
      {
        'key' : 'Actions',
        'value' : this.scripts[0].actions,
      },
    ]

    this.metrics_details = [
      {
      'key' : 'Content',
      'value' : this.scripts[0].wordCount,
      },
      {
      'key' : 'Title',
      'value' : this.scripts[0].titleWordCount,
      },
      {
      'key' : 'Aria label',
      'value' : this.scripts[0].ariaLabelWordCount,
      },
      {
      'key' : 'Tool Tip',
      'value' : this.scripts[0].toolTipWordCount,
      },
      {
      'key' : 'Alt',
      'value' : this.scripts[0].altWordCount,
      },
      {
      'key' : 'Total',
      'value' : this.totalIssues,
      },
    ]
  },
  methods: {
    initialiseUI() {
      if (window.i18N) {
        const scripts = window.i18N;
        console.log(window.i18N);
        this.scripts = scripts.map((scr) => {
          return {
            name: scr.scriptName,
            language: scr.language,
            scenariosCount: scr.scenarios.length,
            tasks: this.getSumOfTasks(scr),
            actions: this.getSumOfActions(scr),
            wordCount: this.getScriptWordCount(scr),
            altWordCount: this.getAltWordCountForScript(scr),
            ariaLabelWordCount: this.getAriaLabelWordCountForScript(scr),
            toolTipWordCount: this.getTooltipWordCountForScript(scr),
            titleWordCount: this.getTitleWordCountForScript(scr),
            scenarios: scr.scenarios.map((scn) => {
              return {

                name: scn.scenarioName,
                activitiesCount: scn.activities.length,
                actions: this.getActionsInScenario(scn),
                wordCount: this.getScenarioWordCount(scn),
                altWordCount: this.getAltWordCountForScenario(scn),
                ariaLabelWordCount: this.getAriaLabelWordCountForScenario(scn),
                toolTipWordCount: this.getTooltipWordCountForScenario(scn),
                titleWordCount: this.getTitleWordCountForScenario(scn),
                activities: scn.activities.map((act) => {
                  return {
                    name: act.activityName,
                    messagesCount: act.i18NDataList.length,
                    wordCount: this.getActivityWordCount(act),
                    altWordCount: this.getAltWordCountForActivity(act),
                    ariaLabelWordCount: this.getAriaLabelWordCountForActivity(act),
                    toolTipWordCount: this.getTooltipWordCountForActivity(act),
                    titleWordCount: this.getTitleWordCountForActivity(act),
                    i18NDataList: act.i18NDataList.map((item) => {
                      return {
                        message: item.insightMessage
                          ? item.insightMessage
                          : "(no message)",
                        image: item.screenshot,
                        elements: item.data ? this.getElements(item.data) : [],
                        alts : item.data ? item.data[0].alts : [],
                        ariaLabels : item.data ? item.data[0].ariaLabels : [],
                        title : item.data ? item.data[0].tittle : [],
                        toolTips : item.data ? item.data[0].toolTips : [],
                        wordCount: item.data ? this.getWordCount(item.data) : 0,
                        altWordCount: this.getAltWordCount(item.data),
                        ariaLabelWordCount: this.getAriaLabelWordCount(item.data),
                        toolTipWordCount: this.getToolTipWordCount(item.data),
                        titleWordCount: this.getTitleWordCount(item.data),
                      };
                    }),
                  };
                }),
              };
            }),
          };
        });
      } else {
        this.$router.push({ name: "upload" });
      }
    },
    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActions(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.i18NDataList.length;
        });
      });
      return sum;
    },
    getActionsInScenario(scn) {
      let sum = 0;

      scn.activities.forEach((act) => {
        sum = sum + act.i18NDataList.length;
      });

      return sum;
    },
    getWordCount(data) {
      let sum = 0;
      
      data.forEach((item) => {
        const { contents} = item;
        contents.forEach((item)=>{
           sum = sum + item.words.length;
        })
       
      });

      return sum;
    },
    getAltWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const {alts } = item;

        alts.forEach((item)=>{
           sum = sum + item.words.length;
        })
        
      });

      return sum;
    },
    getTitleWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const {tittle } = item;
        tittle.forEach((item)=>{
           sum = sum + item.words.length;
        });
        
      });
      
      return sum;
    },
    getAriaLabelWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { ariaLabels} = item;

        ariaLabels.forEach((item)=>{
           sum = sum + item.words.length;
        });
       
      });

      return sum;
    },
    getToolTipWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { toolTips} = item;
        toolTips.forEach((item)=>{
           sum = sum + item.words.length;
        });
        
      });

      return sum;
    },
    getActivityWordCount(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getWordCount(item.data);
      });

      return sum;
    },
    getTooltipWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getToolTipWordCount(item.data);
      });

      return sum;
    },
    getAriaLabelWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getAriaLabelWordCount(item.data);
      });

      return sum;
    },
    getAltWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getAltWordCount(item.data);
      });

      return sum;
    },
    getTitleWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {

        sum = sum + this.getTitleWordCount(item.data);
      });

      return sum;
    },
    getScenarioWordCount(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getActivityWordCount(act);
      });

      return sum;
    },
    getTooltipWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getTooltipWordCountForActivity(act);
      });

      return sum;
    },
    getAriaLabelWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getAriaLabelWordCountForActivity(act);
      });

      return sum;
    },
    getAltWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getAltWordCountForActivity(act);
      });

      return sum;
    },
    getTitleWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getTitleWordCountForActivity(act);
      });

      return sum;
    },
    getScriptWordCount(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getScenarioWordCount(scn);
      });
      return sum;
    },
    getAltWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getAltWordCountForScenario(scn);
      });
      return sum;
    },
    getAriaLabelWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getAriaLabelWordCountForScenario(scn);
      });
      return sum;
    },
    getTooltipWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getTooltipWordCountForScenario(scn);
      });
      return sum;
    },
    getTitleWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getTitleWordCountForScenario(scn);
      });
      return sum;
    },
    getElements(data) {
      let elements = [];
      data[0].contents.forEach((item) => {
        elements.push(item);
      });
      return elements.flat();
    },

    millisecondsToStr(milliseconds) {
      var temp = milliseconds / 1000;
      return `${temp}s`;
    },
    getDimensions(image) {
      return new Promise((resolve, reject) => {
        var img = new Image();
        img.src = image;
        img.onload = () => {
          resolve({ width: img.width, height: img.height });
        };
      });
    },
    showElements(data) {
      this.elements = data;
      this.elementDialog = true;
    },
    async showImage(data) {
      this.baseImage = "";
      this.imageDialog = true;
      this.dimension = await this.getDimensions(data);

      this.baseImage = data;
    },
    isScreenshotBtnDisabled(elements) {
      // if (!location.pathname.includes("index.html")) {
      //   return true;
      // }
      return elements.length === 0 ? false : true;
    },
    getWordsSepreatedByComma(data){
      let words = ""
      if(data.length != 1)
      {
        data.forEach((item)=>{
            words +=item.word+",";
        });
      }
      else
      {
        words = data[0].word;
      }
      return this.decodeHTML(words);
    },
    decodeHTML(data){
      return data ? decodeURI(data) : "";
    },
    trimScriptName(name){
    return name ? name.split('.')[0] : '';
    },
  },
  
  

};
</script>

