<template>
  <div>
    <template>
      <v-tabs
        class="app-tabs"
        show-arrows
      >
        <template
          v-for="item in items"
          class="pa-2"
        >
          <v-hover
            :key="item.displayName"
            v-slot:default="{ hover }"
          >
            <template>
              <div
                :key="item.displayName"
                class="v-tab v-tab-custom"
                :style="{ backgroundColor: item.color}"
                :class="{ active: item.displayName === currentItem }"
              >
                <v-tooltip
                  top
                  open-delay="500"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <div
                      class="v-tab-name"
                      @click="activateItem(item)"
                      v-bind="attrs"
                      v-on="on"
                    >
                      <v-icon
                        color="#D3D3D3"
                        class="tab-icon mr-2"
                        small
                      >
                        {{ item.icon }}
                      </v-icon>
                      {{ truncate(item.displayName, 10) }}
                      <!-- {{ truncate($t(`sidebar.${item.type}`), 10) }} -->
                    </div>
                  </template>
                  <span>{{ item.displayName }}</span>
                </v-tooltip>
                <v-btn
                  v-if="(!item.isDirty)"
                  class="close-btn"
                  @click="closeTab(item)"
                  icon
                  small
                >
                  <v-icon
                    v-if="(item.displayName === currentItem || hover)"
                    small
                  >fas fa-xmark</v-icon>
                </v-btn>

                <v-hover
                  :key="item.displayName + '-hover-close-btn'"
                  v-slot:default="{ hover }"
                >
                  <template>
                    <div style="width: 28px">
                      <div v-if="hover">
                        <v-btn
                          class="close-btn"
                          @click="closeTab(item)"
                          icon
                        >
                          <v-icon small>fas fa-xmark</v-icon>
                        </v-btn>
                      </div>
                    </div>
                  </template>
                </v-hover>
              </div>
            </template>
          </v-hover>
        </template>
      </v-tabs>
    </template>
    <v-tabs-items
      class="app-tabs-items"
      v-model="currentItem"
    >
      <v-tab-item
        class="no-transition"
        v-for="(item, i) in items"
        :key="item.id"
        :value="item.displayName"
      >
        <v-card
          class="app-card"
          flat
        >
          <v-card-text class="pa-3 card-text">
            <keep-alive :key="item.componentName">
              <component
                :id="tabName"
                :is="item.componentName"
                v-on="$listeners"
              ></component>
            </keep-alive>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </div>

</template>

<script>
export default {
  name: "TabWizard",
  props: ["tabRequest"],
  data: () => ({
    currentItem: null,
    currentItemType: "",
    currentItemData: {},
    items: [],
    tabName: "",
  }),
  watch: {
    tabRequest(newVal, oldVal) {
      this.openTab(newVal);
    },
  },
  mounted() {
    this.$root.$on("openTab", (tabObject) => {
      this.openTab(tabObject);
    });

    this.tabName = "Summary Report";
    this.items.push({
      id: this.makeId(5),
      displayName: "Summary Report",
      type: "summaryReport",
      componentName: "SummaryReport",
      icon: "fa-clipboard-list",
      color: "",
      isDirty: false,
    });
    this.$nextTick(() => {
      this.currentItem = "Summary Report";
      this.currentItemType = "summaryReport";
    });

    // if (window.complexity) {
    //   this.tabName = "Complexity";
    //   this.items.push({
    //     id: this.makeId(5),
    //     displayName: this.tabName,
    //     type: "complexity",
    //     componentName: "Complexity",
    //     icon: "fa-chart-bar",
    //     color: "",
    //     isDirty: false,
    //   });
    //   this.$nextTick(() => {
    //     this.currentItem = "Complexity";
    //     this.currentItemType = "complexity";
    //   });
    // } else if (window.accessibility) {
    //   this.tabName = "WCAG";
    //   this.items.push({
    //     id: this.makeId(5),
    //     displayName: this.tabName,
    //     type: "accessibility",
    //     componentName: "Accessibility",
    //     icon: "fa-universal-access",
    //     color: "",
    //     isDirty: false,
    //   });
    //   this.$nextTick(() => {
    //     this.currentItem = "WCAG";
    //     this.currentItemType = "accessibility";
    //   });
    // } else if (window.i18N) {
    //   this.tabName = "Internationalization (I18N)";
    //   this.items.push({
    //     id: this.makeId(5),
    //     displayName: this.tabName,
    //     type: "i18N",
    //     componentName: "Internationalization",
    //     icon: "fa-globe",
    //     color: "",
    //     isDirty: false,
    //   });
    //   this.$nextTick(() => {
    //     this.currentItem = "Internationalization (I18N)";
    //     this.currentItemType = "i18N";
    //   });
    // } else if (window.networkLoad) {
    //   this.tabName = "Network Load";
    //   this.items.push({
    //     id: this.makeId(5),
    //     displayName: this.tabName,
    //     type: "networkLoad",
    //     componentName: "NetworkLoad",
    //     icon: "fa-chart-simple",
    //     color: "",
    //     isDirty: false,
    //   });
    //   this.$nextTick(() => {
    //     this.currentItem = "Network Load";
    //     this.currentItemType = "networkLoad";
    //   });
    // } else if (window.browserPerformance) {
    //   this.tabName = "Browser Performance Metrics";
    //   this.items.push({
    //     id: this.makeId(5),
    //     displayName: this.tabName,
    //     type: "browserPerformance",
    //     componentName: "BrowserMetrics",
    //     icon: "fa-chart-simple",
    //     color: "",
    //     isDirty: false,
    //   });
    //   this.$nextTick(() => {
    //     this.currentItem = "Browser Performance Metrics";
    //     this.currentItemType = "browserPerformance";
    //   });
    // }
  },
  methods: {
    openTab(tabObject) {
      if (Object.keys(tabObject).length === 0) return;

      const isTabOpened = this.items.some(
        (item) => item.displayName === tabObject.displayName
      );
      if (!isTabOpened) {
        this.tabName = tabObject.displayName;
        this.items.push({
          id: this.makeId(5),
          displayName: this.tabName,
          componentName: tabObject.componentName,
          type: tabObject.type,
          icon: tabObject.icon,
          color: tabObject.color,
          isDirty: false,
        });
      }

      this.$nextTick(() => {
        this.tabName = tabObject.displayName;
        this.currentItem = this.tabName;
        this.currentItemType = tabObject.componentName;
        if (tabObject.data) {
          this.currentItemData = tabObject.data;
        }
      });
    },
    activateItem(item) {
      this.$nextTick(() => {
        this.currentItem = item.displayName;
        this.currentItemType = item.componentName;
      });
    },
    closeTab(item) {
      const index = this.items.indexOf(item);
      this.items.splice(index, 1);

      if (this.items.length === 1) {
        this.activateItem(this.items[0]);
      } else if (this.items.length > 1) {
        // this.activateItem(this.items[index]);
        const activeItemIndex = this.items.findIndex(
          (obj) => obj.displayName == this.currentItem
        );
        if (activeItemIndex !== -1) {
          this.activateItem(this.items[activeItemIndex]);
        } else {
          this.activateItem(this.items[0]);
        }
      } else {
        this.currentItem = "";
        this.currentItemType = "";
      }

      if (this.items.length === 0) {
        this.$emit("tabWizardEmpty");
      }
    },
  },
};
</script>


