<template>
  <div class="timeSeriesChart">
    <canvas
      @click="drillDown($event)"
      id="timeSeriesChart"
    ></canvas>

    <v-row class="my-3">
      <v-col
        cols="12"
        class="text-end"
      >
        <v-btn
          :disabled="executionLevel === 'SCRIPT'"
          @click="resetChart()"
          class="mx-3"
        >Reset</v-btn>
        <v-btn
          :disabled="executionLevel === 'SCRIPT'"
          @click="rollUp()"
          class="mx-3"
        >Back</v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
// import execution_detail from "../../execution_detail.json";
export default {
  name: "TimeSeriesChart",
  data() {
    return {
      dataSetJSON: {
        time: {
          label: "seconds",
          backgroundColor: "#aff",
          borderColor: "#09f",
          borderWidth: 2,
          tension: 0.3,
          fill: false,
          radius: 4,
          pointStyle: "rectRounded",
          yAxisID: "y-axis-1",
          type: "line",
        },
      },
      trace: [],
      current: {},
      summary: {},
      executionLevel: "",
      heading: "",
      subTitle: "",
      chartDetails: {},
    };
  },
  mounted() {
    this.summary = execution_detail;
    this.current = this.summary.nestedExecutions[0];
    this.executionLevel = "SCRIPT";
    this.heading = this.current.name;
    this.subTitle = this.executionLevel;
    this.chartDetails = this.getChartDetails();
  },
  methods: {
    getChartDetails() {
      const script = this.summary.nestedExecutions[0];
      const iteration = script.nestedExecutions[0];
      const scenarios = iteration.nestedExecutions;
      return new Chart(document.getElementById("timeSeriesChart"), {
        type: "line",
        data: this.getChartData(scenarios, this.getLabels(scenarios)),
        options: this.getOptions(this.summary),
      });
    },

    getChartData(dataArray, labels) {
      return {
        labels: labels,
        datasets: [
          {
            ...this.dataSetJSON.time,
            data: this.getTime(dataArray),
          },
        ],
      };
    },
    getLabels(dataArray) {
      let labels = [];

      if (this.current.executionLevel === "SCRIPT") {
        dataArray.forEach((scn) => {
          labels.push(scn.name);
        });
        return labels;
      }
      if (this.current.executionLevel === "SCENARIO") {
        dataArray.forEach((act) => {
          labels.push(act.name);
        });
      }
      if (this.current.executionLevel === "ACTIVITY") {
        dataArray.forEach((msg) => {
          labels.push(msg.command);
        });
      }
      return labels;
    },
    getTime(dataArray) {
      let sum;
      // console.log(dataArray);
      if (this.current.executionLevel === "SCRIPT") {
        let activitiesSum = [];
        dataArray.forEach((scn) => {
          sum = 0;
          scn.nestedExecutions.forEach((act) => {
            sum = sum + this.getSeconds(act.executionTime);
          });
          activitiesSum.push(sum);
        });

        return activitiesSum;
      }
      if (this.current.executionLevel === "SCENARIO") {
        let stepsSum = [];
        dataArray.forEach((act) => {
          stepsSum.push(this.getSeconds(act.executionTime));
        });
        return stepsSum;
      }
      if (this.current.executionLevel === "ACTIVITY") {
        let stepTime = [];
        dataArray.forEach((msg) => {
          stepTime.push(this.getSeconds(msg.seconds));
        });
        return stepTime;
      }
    },
    getSeconds(ms) {
      const seconds = Number(((ms % 60000) / 1000).toFixed(0));
      return seconds;
    },
    getDuration(startTime, endTime) {
      return (
        (new Date(startTime).getTime() - new Date(endTime).getTime()) / 60000
      );
    },

    drillDown(click) {
      let points = this.chartDetails.getElementsAtEventForMode(
        click,
        "nearest",
        { intersect: true },
        true
      );
      if (points.length) {
        if (this.current.executionLevel === "SCRIPT") {
          let currentIndex = points[0].index;
          const iteration = this.current.nestedExecutions[0];
          const scenarios = iteration.nestedExecutions;
          this.current = scenarios[currentIndex];
          this.trace.push(currentIndex);
        } else if (this.current.executionLevel === "SCENARIO") {
          let currentIndex = points[0].index;
          const activities = this.current.nestedExecutions;
          this.current = activities[currentIndex];
          this.trace.push(currentIndex);
        }

        this.heading = this.getHeading(this.trace);
        this.updateChart();
      }
    },
    updateChart() {
      let chartData = {};

      if (this.current.executionLevel === "ACTIVITY") {
        chartData.labels = this.getLabels(this.current.actionCountMessages);
        chartData.datasets = [
          {
            ...this.dataSetJSON.time,
            data: this.getTime(this.current.actionCountMessages),
          },
        ];
      } else {
        console.log(this.getTime(this.current.nestedExecutions));
        console.log(this.getLabels(this.current.nestedExecutions));

        chartData.labels = this.getLabels(this.current.nestedExecutions);
        chartData.datasets = [
          {
            ...this.dataSetJSON.time,
            data: this.getTime(this.current.nestedExecutions),
          },
        ];
      }

      this.executionLevel = this.current.executionLevel;

      let config = {
        data: {
          labels: chartData.labels,
          datasets: chartData.datasets,
        },
        options: this.getOptions(this.current),
      };

      this.chartDetails.data = config.data;
      this.chartDetails.options = config.options;
      this.chartDetails.update();
    },

    getHeading(trace) {
      if (this.trace.length > 0) {
        let headings = [this.summary.nestedExecutions[0].name];
        let executionElements =
          this.summary.nestedExecutions[0].nestedExecutions[0];
        for (let i = 0; i < this.trace.length; i++) {
          executionElements = executionElements.nestedExecutions[trace[i]];
          headings.push(executionElements.name);
        }
        return headings.join(" - ");
      }
      this.executionLevel = "SCRIPT";
      return this.summary.nestedExecutions[0].name;
    },
    getOptions(obj) {
      return {
        scales: {
          x: {
            ticks: {
              stepSize: 1,
              beginAtZero: true,
              font: {
                size: 14,
                weight: "bold",
                color: "white",
              },
            },
            grid: {
              color: "#4c4c4c",
            },
          },

          "y-axis-1": {
            ticks: {
              stepSize: 5,
              beginAtZero: true,
              font: {
                size: 12,
              },
              color: "white",
            },
            grid: {
              color: "#4c4c4c",
            },
            title: {
              display: true,
              text: "seconds",
              font: {
                size: 16,
                weight: "bold",
                color: "rgba(50,50,50,0.75)",
              },
            },
          },
        },
        responsive: true,
        interaction: {
          intersect: false,
        },
        plugins: {
          tooltip: {
            mode: "index",
            position: "nearest",
          },
          title: {
            display: true,
            text: this.heading,
            font: {
              size: 18,
              weight: "bold",
              color: "rgba(50,50,50,0.95)",
            },
          },
          subtitle: {
            display: true,
            text: this.executionLevel,
            position: "bottom",
            align: "center",
            font: {
              size: 20,
              weight: "bold",
              color: "rgba(50,50,50,0.95)",
            },
          },
          legend: {
            display: true,
            position: "top",
            labels: {
              boxWidth: 10,
              padding: 25,
              usePointStyle: true,
            },
          },
        },
      };
    },
    resetChart() {
      this.executionLevel = "SCRIPT";
      this.trace = [];
      this.current = this.summary.nestedExecutions[0];
      this.executionLevel = "SCRIPT";
      this.heading = this.current.name;
      this.subTitle = this.executionLevel;

      const script = this.summary.nestedExecutions[0];
      const iteration = script.nestedExecutions[0];
      const scenarios = iteration.nestedExecutions;
      this.chartDetails.data = this.getChartData(
        scenarios,
        this.getLabels(scenarios)
      );
      this.chartDetails.options = this.getOptions(scenarios);
      this.chartDetails.update();
    },

    rollUp() {
      if (this.trace.length < 0) {
        alert("Invalid operation.");
        return;
      }

      if (this.trace.length === 1) {
        this.executionLevel = "SCRIPT";
        this.resetChart();
        return;
      }

      if (this.trace.length === 2) {
        this.executionLevel = "SCENARIO";
        this.current =
          this.summary.nestedExecutions[0].nestedExecutions[0].nestedExecutions[
            this.trace[0]
          ];
        console.log(this.current);
        console.log(this.trace);
        this.trace.pop();
        this.heading = this.getHeading(this.trace);
        this.updateChart();
      }

      // this.current = this.summary;
      // for (let i = 0; i < this.trace.length - 1; i++) {
      //   this.current = this.current.nestedExecutions[trace[i]];
      //   this.executionLevel = this.current.nestedExecutions[0].executionLevel;
      // }

      // this.trace.pop();
      // this.heading = this.getHeading(this.trace);
      // this.updateChart();
    },
  },
};
</script>

<style>
.timeSeriesChart {
  /* height: 500px; */
  width: 80%;
  margin: auto;
}
#timeSeriesChart {
  /* height: 500px !important; */
  width: 100% !important;
}
</style>