import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    enabled: [],
    loaded: false,
  },
  mutations: {
    setEnabled(state, payload) {
      state.enabled = payload;
    },
    setLoaded(state, payload) {
      state.loaded = payload;
    },
  },
});
