import Vue from "vue";
import App from "./App.vue";
import Accessibility from "./components/Accessibility.vue";
import ActionCountChart from "./components/ActionCountChart.vue";
import BrowserMetrics from "./components/BrowserMetrics.vue";
import Complexity from "./components/Complexity.vue";
import FeatureList from "./components/FeatureList.vue";
import Footer from "./components/Footer.vue";
import Header from "./components/Header.vue";
import Internationalization from "./components/Internationalization.vue";
import NetworkLoad from "./components/NetworkLoad.vue";
import SummaryReport from "./components/SummaryReport.vue";
import TimeSeriesChart from "./components/TimeSeriesChart.vue";
import Upload from "./components/Upload.vue";
import vuetify from "./plugins/vuetify";

import UUID from "vue-uuid";

// config
import config from "../config";

// store
import store from "./store";

import router from "./router";
Vue.use(UUID);

Vue.mixin({
  data: function () {
    return {
      config,
    };
  },
  methods: {
    truncate(text, length) {
      if (text && length) {
        if (text.length > length) {
          return text.substring(0, length) + "...";
        } else {
          return text;
        }
      } else return text;
    },
    makeId(length, appendText) {
      var result = "";
      var characters = "abcdefghijklmnopqrstuvwxyza";
      var charactersLength = characters.length;
      for (var i = 0; i < length; i++) {
        result += characters.charAt(
          Math.floor(Math.random() * charactersLength)
        );
      }
      if (appendText) {
        return `${appendText}${result}`;
      }
      return result;
    },
  },
});
Vue.config.productionTip = false;
Vue.component("SummaryReport", SummaryReport);
Vue.component("Complexity", Complexity);
Vue.component("FeatureList", FeatureList);
Vue.component("Internationalization", Internationalization);
Vue.component("Accessibility", Accessibility);
Vue.component("ActionCountChart", ActionCountChart);
Vue.component("TimeSeriesChart", TimeSeriesChart);
Vue.component("Upload", Upload);
Vue.component("NetworkLoad", NetworkLoad);
Vue.component("Header", Header);
Vue.component("Footer", Footer);
Vue.component("BrowserMetrics", BrowserMetrics);

new Vue({
  router,
  store,
  vuetify,
  render: function (h) {
    return h(App);
  },
}).$mount("#app");
