<template>
  <v-navigation-drawer
    expand-on-hover
    :mini-variant="mini"
    permanent
    app
  >
    <v-list>
      <v-list-item
        @click="openWindow(component)"
        v-for="(component, index) of config.components"
        :key="component.componentName"
        :disabled="isDisabled(component.type)"
      >
        <v-list-item-icon>
          <v-icon :disabled="isDisabled(component.type)">{{ component.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-title>{{ component.displayName }}</v-list-item-title>
        <!-- <v-list-item-title>{{ $t(`sidebar.${component.type}`) }}</v-list-item-title> -->
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { colors } from 'vuetify/lib';

export default {
  name: "Aside",
  data() {
    return {
      mini: false,
    };
  },
  mounted() {},
  methods: {
    openWindow(tabObject) {
      if (
        (tabObject.children && tabObject.children.length === 0) ||
        !tabObject.children
      ) {
        this.$emit("createWindow", tabObject);
      }
    },
    isDisabled(type) {
      return !this.$store.state.enabled.includes(type);
    },
  },
};
</script>
<style>
.theme--light.v-navigation-drawer{
 background-color: #05758a !important;

}
 .theme--light.v-list-item--disabled
{
  color: #fff !important;
  opacity: 0.3;
}

.theme--light.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled){
  color: #fff!important;
} 
.theme--light.v-icon {
  color: #6bd69b !important ;
}

</style>