<template>
  <v-app>
    <router-view
      v-if="$route.name !== 'home'"
      :key="$route.fullPath"
    ></router-view>
    <div v-else>
      <Header />
      <Aside v-on:createWindow="receivedCreateWindow" />
      <v-main>
        <v-container fluid>
          <TabWizard
            v-if="$store.state.loaded"
            :tabRequest="tabRequest"
          />
        </v-container>
        <Footer />
      </v-main>

    </div>
  </v-app>
</template>

<script>
import Aside from "./components/Aside.vue";
import TabWizard from "./components/TabWizard.vue";
import Footer from "./components/Footer.vue";
import Header from "./components/Header.vue";

export default {
  name: "App",
  components: {
    Aside,
    TabWizard,
    Footer,
    Header,
  },
  data: () => ({
    tabRequest: {},
    test: false,
    enabled: [],
  }),
  mounted() {
    if (location.pathname.includes("index.html")) {
      this.loadScripts();
    } else {
      if (this.$router.history.current.path !== "/upload") {
        this.$router.push({ name: "upload" });
      }
    }
  },
  methods: {
    receivedCreateWindow(tabObject) {
      this.tabRequest = {
        id: Math.random(),
        ...tabObject,
      };
    },
    clearTabRequest() {
      this.tabRequest = {};
    },
    loadScripts() {
      const self = this;
      const names = [
        "complexity",
        "i18N",
        "accessibility",
        "networkLoad",
        "header",
        "browserPerformance",
      ];
      names.forEach((name) => {
        let script = document.createElement("script");
        script.type = "text/javascript";

        script.addEventListener("load", function (event) {
          if (window.complexity) {
            if (!self.enabled.includes("complexity")) {
              self.enabled.push("complexity");
              self.$store.commit("setEnabled", self.enabled);
            }
          }
          if (window.accessibility) {
            if (!self.enabled.includes("accessibility")) {
              self.enabled.push("accessibility");
              self.$store.commit("setEnabled", self.enabled);
            }
          }
          if (window.i18N) {
            if (!self.enabled.includes("i18N")) {
              self.enabled.push("i18N");
              self.$store.commit("setEnabled", self.enabled);
            }
          }
          if (window.networkLoad) {
            if (!self.enabled.includes("networkLoad")) {
              self.enabled.push("networkLoad");
              self.$store.commit("setEnabled", self.enabled);
            }
          }
          if (window.header) {
            if (!self.enabled.includes("header")) {
              self.enabled.push("header");
              self.$store.commit("setEnabled", self.enabled);
            }
          }
          if (window.browserPerformance) {
            if (!self.enabled.includes("browserPerformance")) {
              self.enabled.push("browserPerformance");
              self.$store.commit("setEnabled", self.enabled);
            }
          }
          if (self.$router.history.current.path !== "/") {
            self.$router.push({ name: "home" });
          }
        });

        script.addEventListener("error", function (event) {
          if (self.enabled.length === 0) {
            if (self.$router.history.current.path !== "/upload") {
              self.$router.push({ name: "upload" });
            }
          }
        });
        script.src = `./execution/${name}.js`;
        document.head.appendChild(script);
      });

      self.enabled.push("summaryReport");

      setTimeout(() => {
        if (this.enabled.length > 0) {
          this.$store.commit("setLoaded", true);
        } else {
          if (this.$router.history.current.path !== "/upload") {
            this.$router.push({ name: "upload" });
          }
        }
      }, 300);
    },
  },
};
</script>

