import Vue from "vue";
import VueRouter from "vue-router";
import App from "../App.vue";
import Upload from "../components/Upload.vue";
import { publicPath } from "../../vue.config";

Vue.use(VueRouter);

export default new VueRouter({
  base: publicPath,
  routes: [
    {
      path: "/",
      name: "home",
      component: App,
    },
    {
      path: "/upload",
      name: "upload",
      component: Upload,
    },
  ],
});
